* {
    box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

#root {
    display: -webkit-flex;
    display: flex;
    min-height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
  }

  main {
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
  }

  nav {
    margin: 0 !important;
  }

footer {
  padding: 20px;
}

.comment-image {
  margin-top: 50px;
  width: 150px !important;
  border-radius: 50% !important;
}

button {
  margin-top: 20px;
}
.fixed-action-btn {
    right: 100px;
    bottom: 100px;
}

.modal-close {
    margin-right: 20px !important;
}
.post {
    margin-top: 40px;
}

input[type='submit'] {
    padding: 10px;
    border: none;
}
.modal-wrapper {
    background:#e0f2f1 ;
    border: 3px solid #004d40;
    box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2), 0 7px 20px 0 rgba(0,0,0,0.17);
    margin-left:  10%; ;
    transition: all .8s;
    width: 40%; 
    position: fixed;
    z-index: 1;
    height: 65%;
}

.btn-mrg-1{
    margin-right: 150px;
    position:absolute;
    bottom:10px;
    right:50px;
}

.btn-mrg-2{
    margin-right: 40px;
    position:absolute;
    bottom:10px;
    right:50px;
}

#biography {
    width: 80%;
}

#img_url {
